import { useSelector } from "react-redux";
import store from "./Store.js";
import "../styles/forecast.scss"
import { temperatureIcon, windIcon, windDirectionIcon, cloudCoverage, sunrise, sunset } from "./constants.js";
import { weatherIcons } from "./constants.js";
import { GetWindDirection } from "../functions/GetWindDirection"
import { GetCountryName } from "../functions/GetCountryName.js";

const CityCurrentWeather = () => { 

    const currentWeatherData = state => state.currentWeather;
    const  cityCurrentWeatherData = useSelector(currentWeatherData);

    const currentStoreTimezone = state => state.timezone;
    const storeTimezone = useSelector(currentStoreTimezone)

    
    

    if (cityCurrentWeatherData != null) {    
          
        
    if (cityCurrentWeatherData !== "error") {
       
        let weatherIcon;
        let weatherIconColor;
       
        // Checking if weather is 'clouds' and cloud coverage is <= 50%. Defining a specific icon for this case
       if (cityCurrentWeatherData.weather[0].main == "Clouds" && cityCurrentWeatherData.clouds.all <= 50 ) {
            weatherIcon = weatherIcons.filter(element => element.label === "SunClouds")[0].icon
            weatherIconColor = weatherIcons.filter(element => element.label === "SunClouds")[0].label
        }
        else {
            weatherIcon = weatherIcons.filter(element => element.label === cityCurrentWeatherData.weather[0].main)[0].icon
            weatherIconColor = weatherIcons.filter(element => element.label === cityCurrentWeatherData.weather[0].main)[0].label
        }

        /*Calculating current city Local time*/
        const localTime = cityCurrentWeatherData.dt + storeTimezone + new Date(cityCurrentWeatherData.dt*1000).getTimezoneOffset()*60;
        const current = new Date(localTime*1000).toLocaleTimeString("fr-FR")
        const currentTimeHHMM = current.substring(0, 5)
        /*calculating current city sunrise time*/
        const sunriseTime = cityCurrentWeatherData.sys.sunrise + storeTimezone + new Date(cityCurrentWeatherData.dt*1000).getTimezoneOffset()*60;
        const currentSunrise = new Date(sunriseTime*1000).toLocaleTimeString("fr-FR")
        const currentSunriseHHMM = currentSunrise.substring(0, 5)
        /*calculating current city sunset time*/
        const sunsetTime = cityCurrentWeatherData.sys.sunset + storeTimezone + new Date(cityCurrentWeatherData.dt*1000).getTimezoneOffset()*60;
        const currentSunset = new Date(sunsetTime*1000).toLocaleTimeString("fr-FR")
        const currentSunsetHHMM = currentSunset.substring(0, 5)

        

        let gust;
        
        if (cityCurrentWeatherData.wind.gust) {
            gust = " - "+Math.round(cityCurrentWeatherData.wind.gust)
        } else {
            gust = "";
        }

    return (
            <div className = "forecast__card forecast__card--current">
                <div className = "forecast__card__time-weather forecast__card__time-weather--current">
                    <div className = "forecast__card__time-weather__time">{currentTimeHHMM}</div>
                    <i className={weatherIcon+" forecast__card__time-weather__icon forecast__card__time-weather__icon--"+weatherIconColor+" forecast__card__time-weather__icon--big"}></i>
                </div>
                {/*<div>{props.forecast.weather[0].description}</div>*/}
                <div className = "forecast__card__city-info-container">
                    <div className = "forecast__card__city">{cityCurrentWeatherData.name}</div>
                    <div className = "forecast__card__country">{GetCountryName(cityCurrentWeatherData.sys.country)}</div>
                </div>
                    {/*<div>{cityCurrentWeatherData.weather[0].main}</div>*/}
                
                <div className = "forecast__card__data-container">
                    <div className = "forecast__card__parameter forecast__card__parameter--header">
                        <i className={temperatureIcon +" forecast__card__parameter__icon forecast__card__parameter__icon--temp"}></i>
                        <div className = "forecast__card__parameter__value">{Math.round(cityCurrentWeatherData.main.temp)+" °C"}</div>
                    </div>
                    <div className = "forecast__card__parameter forecast__card__parameter--header">
                        <i className={windIcon +" forecast__card__parameter__icon forecast__card__parameter__icon--wind"}></i>
                        <div className = "forecast__card__parameter__value">{Math.round(cityCurrentWeatherData.wind.speed)+gust+" m/s"}</div>
                    </div>
                    <div className = "forecast__card__parameter forecast__card__parameter--header">
                        <i className={cloudCoverage +" forecast__card__parameter__icon forecast__card__parameter__icon--cloud-coverage"}></i>
                        <div className = "forecast__card__parameter__value">{cityCurrentWeatherData.clouds.all+" %"}</div>
                    </div>
                    <div className = "forecast__card__parameter forecast__card__parameter--header">
                        <i className={windDirectionIcon +" forecast__card__parameter__icon forecast__card__parameter__icon--wind-direction"}></i>
                        <div className = "forecast__card__parameter__value forecast__card__parameter__value--header">{GetWindDirection(cityCurrentWeatherData.wind.deg)+" ("+cityCurrentWeatherData.wind.deg+"°)"}</div>
                    </div>
                    <div className = "forecast__card__parameter forecast__card__parameter--header">
                        <i className={sunrise +" forecast__card__parameter__icon forecast__card__parameter__icon--sunrise"}></i>
                        <div className = "forecast__card__parameter__value">{currentSunriseHHMM}</div>
                    </div>
                    <div className = "forecast__card__parameter forecast__card__parameter--header">
                        <i className={sunset +" forecast__card__parameter__icon forecast__card__parameter__icon--sunset"}></i>
                        <div className = "forecast__card__parameter__value">{currentSunsetHHMM}</div>
                    </div>

                </div>
            </div>)
        } 
        else {
            return (
                <div className = "forecast__card forecast__card--current">
                    <div className = "forecast__card__time-weather__error">{"Désolé, je n'ai pas pu trouver cette ville :("}</div>
                </div>
            )
        }}
        else {
            return null;
        }

    
}
export default CityCurrentWeather